//CSS
import './css/MySubSubjects.css';
//IMPORT
import React from 'react';
import { Row, Col } from 'antd';
import * as mySubSubjectDAO from './dao/MySubSubjectDAO';
import { Link } from 'react-router-dom';
import { Card } from 'antd';
import { Button } from 'antd';
import { Icon } from 'antd';

function MySubSubjects(props){
  const subject = props.subject;
  const subsubjects = mySubSubjectDAO.getSubSubjects(subject.key);
  
  return(
    <Row type="flex"  align="top">
      {
        subsubjects.map(function(item, key){
          return (
            <Col key={key} order={key} xs={24} sm={12} md={8} lg={6} xl={6}>
              <Link to={"/conteudo/" + subject.key +"/"+item.key} key={item.id}>
              <div className="mysubsubjects-div-center">
                <Card
                  hoverable
                  className="mysubsubjects-div-item"
                  cover={<div className="mysubsubjects-icon" style={{backgroundImage: 'url('+subject.img+')'}}></div>}>
                  <p className="mysubsubjects-title">{item.title}</p>
                  <Button type="primary" ghost style={{ width: "100%"}}><Icon type="smile" /> Aprenda mais</Button>
                </Card>
              </div>
              </Link>
            </Col>  
          );
        })
      }
    </Row>
  );
}

export default MySubSubjects;


