//CSS
import './css/MyContent.css';
//IMPORT
import React from "react";
import MarkdownIt from 'markdown-it';
import markdownItHighlight from 'markdown-it-highlight';
import MarkdownItTexmath from 'markdown-it-texmath';
import katex from 'katex';
import dompurify from 'dompurify';
import { Link } from 'react-router-dom'
import { Breadcrumb } from 'antd';
import * as mySubjectDAO from './dao/MySubjectDAO';
import * as mySubSubjectDAO from './dao/MySubSubjectDAO';


function MarkdownWithLatexAndCode(props){
  const sanitizer = dompurify.sanitize;
  
  let mdParser = new MarkdownIt({
    html: true,
    linkify: true,
    typographer: true    
  })
  .use(markdownItHighlight)
  .use(MarkdownItTexmath.use(katex));

  return <div dangerouslySetInnerHTML={{__html: sanitizer(mdParser.render(props.value))}}/>;
}


function MyContent(props){
  const subject = mySubjectDAO.getSubject(props.match.params.subject);
  const subsubject = mySubSubjectDAO.getSubSubject(subject.key, props.match.params.subsubject);
  const text = mySubSubjectDAO.getText(subsubject.key);

  //console.log(props.match.params);
  return (
    <div className="mycontent-div-main">    
      <div className="mycontent-div-content">
        <Breadcrumb>
          <Breadcrumb.Item>
            <Link to="/">Início</Link>
          </Breadcrumb.Item>
          <Breadcrumb.Item>
            <Link to="/conteudo">Conteúdo</Link>
          </Breadcrumb.Item>
          <Breadcrumb.Item>
            <Link to={'/conteudo/'+subject.key}>{subject.title}</Link>
          </Breadcrumb.Item>
          <Breadcrumb.Item>
            {subsubject.title}
          </Breadcrumb.Item>
        </Breadcrumb>      
        <MarkdownWithLatexAndCode value = {text}/>
      </div>
    </div>
  );
} 

export default MyContent;